#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
main {
  flex-grow: 1;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.strIngredientSelectors {
  display: flex;
  flex-direction: column;
  align-content: stretch;
  width: 300px;
  margin: auto;
}

.select {
  border-radius: 10px;
  font-size: 1.8vh;
  padding: 0.5vh;
  cursor: pointer;
  margin-top: 2vh;
  background-color: #101728;
  box-shadow:
              0 0 .5rem #1f8187,
              inset 0 0 .5rem #fff,
              0 0 2rem var(--neon-border-color),
              inset 0 0 2rem var(--neon-border-color),
              0 0 4rem var(--neon-border-color),
              inset 0 0 4rem var(--neon-border-color); 
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  background-image: url("component/Images/backgroundimg.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.hello {
  color: rgb(255, 255, 255); 
}
.pagination {
  display: flex;
  justify-content: center;
}
.pagination .pagebutton {
  margin: 1.5vh 0.5vh;
  padding: 0.9vh 6vh;
  background-color: #101728;
  box-shadow:
          0 0 .5rem #1f8187,
          inset 0 0 .5rem #fff,
          0 0 2rem var(--neon-border-color),
          inset 0 0 2rem var(--neon-border-color),
          0 0 4rem var(--neon-border-color),
          inset 0 0 4rem var(--neon-border-color);
  color: white;
  font-size: 1.5vh;
  border-radius: 10px;
  cursor: pointer;
}
.pagebutton:hover {
  color: #b62456;
}

